import React, { useState, useEffect } from "react";
import {
  BrowserRouter as Router,
  Route,
  Routes,
  Navigate,
} from "react-router-dom";
import axios from "axios";
import Login from "./LoginUser";
import AdminDashboard from "./AdminDashboard/AdminDashboard";
import SubAdminDashboard from "./SubAdminDashboard/SubAdminDashboard";
import UserDashboard from "./UserDashboard/UserDashboard";
import "./App.css";
import SubAdminList from "./AdminDashboard/SubAdminList";
import SubAdminRegistration from "./AdminDashboard/SubAdminRegistration";
import SubAdminTransaction from "./AdminDashboard/SubAdminTransaction ";
import SubAdinUserRegister from "./SubAdminDashboard/SubAdminUserRegister";
import UserList from "./SubAdminDashboard/UserList";
import UserTransfer from "./SubAdminDashboard/UserTransfer";
import CoinGame from "./UserDashboard/CoinGame";
function App() {
  const [data, setData] = useState(null);
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const token = localStorage.getItem("token"); // Retrieve token from localStorage
        const response = await axios.get("api/auth/success", {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });
        setData(response.data);
        setIsLoading(false);
      } catch (error) {
        setError(error);
        setIsLoading(false);
      }
    };

    fetchData();
  }, []);

  return (
    <Router>
      <Routes>
        <Route path="/" element={<Navigate to="/login" />} />
        <Route path="/login" element={<Login />} />

        {data?.role === "admin" && (
          <>
            <Route path="/admin-dashboard" element={<AdminDashboard />} />
            <Route path="/all-sub-users" element={<SubAdminList />} />
            <Route
              path="/sub-admin-trnasfer"
              element={<SubAdminTransaction />}
            />
            <Route
              path="/register-subadmin"
              element={<SubAdminRegistration />}
            />
          </>
        )}

        {data?.role === "sub-admin" && (
          <>
            <Route
              path="/sub-admin-dashboard"
              element={<SubAdminDashboard />}
            />
            <Route
              path="/register-user-form"
              element={<SubAdinUserRegister />}
            />
            <Route path="/all-users" element={<UserList />} />
            <Route path="/user-transfer" element={<UserTransfer />} />
          </>
        )}
        {data?.role === "user" && (
          <>
            <Route path="/user-dashboard" element={<UserDashboard />} />
            <Route path="/game" element={<CoinGame />} />
          </>
        )}
        {/* <Route path="*" element={<Login />} /> */}
      </Routes>
    </Router>
  );
}

export default App;
