import React, { useState, useEffect } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { Visibility, VisibilityOff } from "@mui/icons-material";
import ResponsiveDrawer from "../Components/ResponsivedrawerSubAdmin";

const SubAdminRegistration = () => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [successMessage, setSuccessMessage] = useState("");
  const [errorMessage, setErrorMessage] = useState("");
  const [subAdmins, setSubAdmins] = useState([]);
  const [openDrawer, setOpenDrawer] = useState(false);
  const navigate = useNavigate();
  const [showPassword, setShowPassword] = useState(false);

  const handleDrawerToggle = () => {
    setOpenDrawer(!openDrawer);
  };

  useEffect(() => {
    const fetchSubAdmins = async () => {
      try {
        const token = localStorage.getItem("token");
        const response = await axios.get("api/auth/success", {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });
        setSubAdmins(response.data);
      } catch (error) {
        console.error("Error fetching sub-admins:", error);
      }
    };

    fetchSubAdmins();
  }, []);

  const handleLogout = async () => {
    try {
      await axios.post("api/auth/logout");
      localStorage.removeItem("token");
      navigate("/login");
      window.location.reload();
    } catch (error) {
      console.error("Logout error:", error);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.post(
        "api/auth/register-user",
        { username, password },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );

      const token = response.data.token;

      localStorage.setItem("token", token);

      setSuccessMessage(response.data.message);
      setErrorMessage("");

      // Check if the user role is subadmin
      const isSubAdmin = subAdmins.some(
        (subAdmin) => subAdmin.username === username
      );

      if (isSubAdmin) {
        // If user role is subadmin, register the user
        await axios.post(
          "api/auth/register-user",
          { username, password },
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        );
      }

      // Reset username and password fields
      setUsername("");
      setPassword("");
    } catch (error) {
      setSuccessMessage("");
    }
  };

  return (
    <div>
      <ResponsiveDrawer
        open={openDrawer}
        handleDrawerToggle={handleDrawerToggle}
        handleLogout={handleLogout}
      />

      <form className="form-login" onSubmit={handleSubmit}>
        <div className="fw-bolder fs-5 text-white">Register User Form</div>
        {successMessage && (
          <p className="fs-6 text-success">{successMessage}</p>
        )}
        {errorMessage && <p className="fs-6 text-danger">{errorMessage}</p>}
        <div>
          <label className="fw-bolder text-white">Username:</label>
          <input
            type="text"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            required
          />
        </div>
        <div>
          <label className="fw-bolder text-white">Password:</label>
          <div className="d-flex">
            <input
              type={showPassword ? "text" : "password"}
              placeholder="Password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className="form-password"
            />
            <div className="input-group-text">
              {showPassword ? (
                <VisibilityOff onClick={() => setShowPassword(false)} />
              ) : (
                <Visibility onClick={() => setShowPassword(true)} />
              )}
            </div>
          </div>
        </div>
        <button type="submit">Register</button>
      </form>
    </div>
  );
};

export default SubAdminRegistration;
