import React, { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { Visibility, VisibilityOff } from "@mui/icons-material";
const Login = () => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState(null);
  const [role, setRole] = useState("");
  const [showPassword, setShowPassword] = useState(false);
  const navigate = useNavigate();

  const handleLogin = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.post("api/auth/login", {
        username,
        password,
      });

      // Assuming the response contains a JWT token and role
      localStorage.setItem("token", response?.data?.token);
      setRole(response?.data?.role); // Update role in state based on response

      // Redirect based on user role
      if (response?.data?.role === "admin") {
        navigate("/admin-dashboard");
        window.location.reload();
      } else if (response?.data?.role === "sub-admin") {
        navigate("/sub-admin-dashboard");
        window.location.reload();
      } else {
        navigate("/user-dashboard");
        window.location.reload();
      }

      // Redirect or handle login success as needed
    } catch (error) {
      setError("Invalid username or password");
    }
  };
  return (
    <>
      <div className="background-dark">
        <form className="form-login" onSubmit={handleLogin}>
          <div className="fw-bolder fs-5 text-white">Login Form</div>
          {error && <div className="text-danger ">{error}</div>}
          <div>
            <input
              type="text"
              placeholder="Username"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              className="w-100"
            />
          </div>
          <div>
            <div className=" d-flex">
              <div>
                {" "}
                <input
                  type={showPassword ? "text" : "password"} // Toggle password visibility
                  placeholder="Password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  className="form-password" // Added Bootstrap form-control class
                />
              </div>
              <div className="input-group-text">
                {" "}
                {/* Added input-group-text class for Bootstrap styling */}
                {showPassword ? (
                  <VisibilityOff onClick={() => setShowPassword(false)} />
                ) : (
                  <Visibility onClick={() => setShowPassword(true)} />
                )}
              </div>
            </div>
          </div>
          <div className="w-100 mt-4">
            <button type="submit">Login</button>
          </div>
        </form>
      </div>
    </>
  );
};

export default Login;
