// SubAdminPanel.js

import React, { useState, useEffect } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import ResponsiveDrawer from "../Components/ResponsivedrawerSubAdmin";

const SubAdminPanel = () => {
  const [subAdminData, setSubAdminData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [openDrawer, setOpenDrawer] = React.useState(false);
  const navigate = useNavigate();
  const handleDrawerToggle = () => {
    setOpenDrawer(!openDrawer);
  };
  useEffect(() => {
    const fetchSubAdminData = async () => {
      try {
        const token = localStorage.getItem("token");

        // Make a GET request to fetch sub-admin data
        const response = await axios.get("api/auth/success", {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });

        // Extract sub-admin data from the response
        const subAdmin = response.data.data;

        // Set the fetched sub-admin data to state
        setSubAdminData(subAdmin);
        setLoading(false);
      } catch (error) {
        console.error("Error fetching sub-admin data:", error);
      }
    };

    fetchSubAdminData();
  }, []);

  const handleLogout = async () => {
    try {
      // Send logout request to backend
      await axios.post("api/auth/logout");

      // Clear JWT token from local storage
      localStorage.removeItem("token");

      // Redirect to login page after successful logout
      navigate("/login");
      window.location.reload();
    } catch (error) {
      console.error("Error logging out:", error);
    }
  };

  return (
    <>
      <ResponsiveDrawer
        open={openDrawer}
        handleDrawerToggle={handleDrawerToggle}
        handleLogout={handleLogout}
      />
      <div className="text-white container app-mt-70">
        <h2>Sub Admin Panel</h2>
        {loading ? (
          <p>Loading...</p>
        ) : (
          <div>
            <p>Username: {subAdminData.username}</p>
            <p>Amount: {subAdminData.balance}</p>

            <button onClick={handleLogout}>Logout</button>
          </div>
        )}
      </div>
    </>
  );
};

export default SubAdminPanel;
