import React, { useEffect, useState } from "react";

import UserResponsiveBar from "../Components/UserRespoinsiveBar";
import { useNavigate } from "react-router-dom";
import axios from "axios";
const CoinGame = () => {
  const [coinResult, setCoinResult] = useState(null);
  const [timer, setTimer] = useState(40); // Start timer from 40 seconds for demonstration
  const [countdownInterval, setCountdownInterval] = useState(null); // Interval ID for countdown
  const [selectedOption, setSelectedOption] = useState(""); // Selected option
  const [history, setHistory] = useState([]); // History of results
  const [selectedAmount, setSelectedAmount] = useState(10); // Selected amount
  const [inputDisabled, setInputDisabled] = useState(false); // Whether to disable input
  const [selectDisabled, setSelectDisabled] = useState(false); // Whether to disable select option
  const [insufficientBalance, setInsufficientBalance] = useState(false); // Flag to track insufficient balance
  const [user, setUser] = useState({ balance: 0 }); // Initialize user state

  useEffect(() => {
    const storedHistory =
      JSON.parse(localStorage.getItem("coinGameHistory")) || [];
    setHistory(storedHistory);
    startNewGame(); // Start a new game when component mounts

    // Mock user data
    setUser({ balance: 100 }); // Set initial balance for demonstration purposes

    // Add event listener for beforeunload
    window.addEventListener("beforeunload", handleBeforeUnload);

    return () => {
      // Clean up event listener on component unmount
      window.removeEventListener("beforeunload", handleBeforeUnload);
    };
  }, []);

  useEffect(() => {
    if (timer === 0) {
      handleGameEnd();
    }
    if (timer <= 30) {
      setInputDisabled(true); // Disable input when 30 seconds left
      setSelectDisabled(true); // Disable select option when 30 seconds left
    } else {
      setInputDisabled(false); // Disable input when 30 seconds left
      setSelectDisabled(false);
    }
  }, [timer]);

  const startNewGame = () => {
    setTimer(120); // Reset timer to 40 seconds for demonstration
    setCoinResult(null); // Reset coin result
    setSelectedOption(""); // Reset selected option
    setSelectedAmount(null); // Reset selected amount (radio buttons)
    setInputDisabled(false); // Enable input when starting a new game
    setSelectDisabled(false); // Enable select option when starting a new game
    setInsufficientBalance(false); // Reset insufficient balance flag
    clearInterval(countdownInterval); // Clear previous interval
    const intervalId = setInterval(() => {
      setTimer((prevTimer) => prevTimer - 1); // Decrement timer by 1 second
    }, 2000);
    setCountdownInterval(intervalId);
  };

  const handleGameEnd = async () => {
    try {
      const result =
        Math.random() < 0.33
          ? "Heads"
          : Math.random() < 0.66
          ? "Tails"
          : "Draw";
      setCoinResult(result);

      // Calculate amount won or lost based on the game result
      let amount = 0;
      let isWin = false; // New variable to indicate win or loss
      if (result === selectedOption) {
        amount = selectedAmount;
        isWin = true; // User wins
      } else if (result === "Draw") {
        amount = 0;
      } else {
        amount = -selectedAmount;
      }

      // Show alert if balance is insufficient
      if (amount > 0 && amount > user.balance) {
        setInsufficientBalance(true);
        return;
      }

      const token = localStorage.getItem("token");
      // Update user's balance using backend API
      const response = await fetch("api/auth/updateBalance", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify({ amount, isWin }), // Include isWin parameter
      });

      if (response.ok) {
        addToHistory(result, selectedOption, selectedAmount);
        startNewGame();
      } else {
        console.error("Failed to update balance:", response.statusText);
      }
    } catch (error) {
      console.error("Error handling game end:", error);
    }
  };

  const handleGameStart = () => {
    // Show alert if balance is insufficient
    if (selectedAmount > user.balance) {
      setInsufficientBalance(true);
    }
  };

  const addToHistory = (result, selectedOption, selectedAmount) => {
    const timestamp = new Date().toLocaleString();
    let message = "";
    if (result === selectedOption) {
      message = "You win!";
    } else if (result === "Draw") {
      message = "It's a draw!";
    } else {
      message = "You lost!";
    }
    const entry = {
      result,
      selectedOption,
      selectedAmount,
      message,
      timestamp,
    };
    setHistory((prevHistory) => [entry, ...prevHistory]); // Add new entry to the beginning of the array
    localStorage.setItem(
      "coinGameHistory",
      JSON.stringify([entry, ...history])
    ); // Update local storage
  };

  // Format timer display to actual time
  const formatTime = (timeInSeconds) => {
    const minutes = Math.floor(timeInSeconds / 60);
    const seconds = timeInSeconds % 60;
    return `${minutes < 10 ? "0" : ""}${minutes}:${
      seconds < 10 ? "0" : ""
    }${seconds}`;
  };

  // Handle beforeunload event
  const handleBeforeUnload = (event) => {
    // Cancel the event
    event.preventDefault();
    // Chrome requires returnValue to be set
    event.returnValue = "";
  };

  const [openDrawer, setOpenDrawer] = React.useState(false);
  const navigate = useNavigate(); // Correctly use useNavigate hook here

  const handleDrawerToggle = () => {
    setOpenDrawer(!openDrawer);
  };
  const handleLogout = async () => {
    try {
      // Send logout request to backend
      await axios.post("api/auth/logout");

      // Clear JWT token from local storage
      localStorage.removeItem("token");

      // Redirect to login page after successful logout
      navigate("/login");
      window.location.reload();
    } catch (error) {}
  };
  return (
    <>
      <UserResponsiveBar
        open={openDrawer}
        handleDrawerToggle={handleDrawerToggle}
        handleLogout={handleLogout}
      />
      <div className="container app-mt-70 text-white text-center">
        <h1>Coin Game</h1>
        <p>Time: {formatTime(timer)}</p>
        <select
          disabled={selectDisabled}
          onChange={(e) => setSelectedOption(e.target.value)}
          value={selectedOption}
        >
          <option value="">Select Next Match</option>
          <option value="Heads">Heads</option>
          <option value="Tails">Tails</option>
          <option value="Draw">Draw</option> {/* Added option for draw */}
        </select>
        <div>
          <p>Select Amount:</p>
          {[10, 50, 100, 200, 300, 500, 1000].map((amount) => (
            <label key={amount}>
              <input
                type="radio"
                value={amount}
                checked={selectedAmount === amount}
                onChange={() => setSelectedAmount(amount)}
                disabled={inputDisabled}
                onClick={handleGameStart}
              />
              {amount}
            </label>
          ))}
        </div>

        {coinResult && <p>Result: {coinResult}</p>}

        {/* Show alert for insufficient balance */}
        {insufficientBalance && (
          <div style={{ color: "red" }}>
            Insufficient balance to play the game
          </div>
        )}

        <h2>History</h2>
        <ul style={{ height: "150px", overflowY: "hidden" }}>
          {history.map((entry, index) => (
            <li key={index}>
              {`${index + 1}. ${entry.result} - ${entry.timestamp} - ${
                entry.message
              } - ${entry.selectedAmount}`}
            </li>
          ))}
        </ul>
      </div>
    </>
  );
};

export default CoinGame;
