import React from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import CssBaseline from "@mui/material/CssBaseline";
import Divider from "@mui/material/Divider";
import Drawer from "@mui/material/Drawer";
import IconButton from "@mui/material/IconButton";
import GroupIcon from "@mui/icons-material/Group";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import MailIcon from "@mui/icons-material/Mail";
import MenuIcon from "@mui/icons-material/Menu";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import LogoutIcon from "@mui/icons-material/Logout";
import { NavLink } from "react-router-dom";

const drawerWidth = 240;

const ResponsiveDrawer = ({ open, handleDrawerToggle, handleLogout }) => {
  const drawer = (
    <div style={{ backgroundColor: "#ffeddb" }}>
      <Toolbar />

      <List>
        <NavLink to="/all-sub-users">
          <List>
            <ListItem>
              <ListItemIcon sx={{ color: "#333" }}>
                <GroupIcon />
              </ListItemIcon>
              <ListItemText
                primary="See All Sub Admin"
                sx={{ color: "#333" }}
              />
            </ListItem>
          </List>
        </NavLink>
      </List>

      <List>
        <NavLink to="/sub-admin-trnasfer">
          <List>
            <ListItem>
              <ListItemIcon sx={{ color: "#333" }}>
                <GroupIcon />
              </ListItemIcon>
              <ListItemText primary="Transfer Amount" sx={{ color: "#333" }} />
            </ListItem>
          </List>
        </NavLink>
      </List>
      <List>
        <NavLink to="/register-subadmin">
          <List>
            <ListItem>
              <ListItemIcon sx={{ color: "#333" }}>
                <GroupIcon />
              </ListItemIcon>
              <ListItemText
                primary="Register SubAdmin"
                sx={{ color: "#333" }}
              />
            </ListItem>
          </List>
        </NavLink>
      </List>
      <Divider />
      <List>
        <ListItem button key="Logout" onClick={handleLogout}>
          <ListItemIcon sx={{ color: "#333" }}>
            <LogoutIcon />
          </ListItemIcon>
          <ListItemText primary="Logout" sx={{ color: "#333" }} />
        </ListItem>
      </List>
    </div>
  );

  return (
    <Box sx={{ display: "flex" }}>
      <CssBaseline />
      <AppBar
        position="fixed"
        sx={{
          width: { sm: `calc(100% - ${drawerWidth}px)` },
          ml: { sm: `${drawerWidth}px` },
        }}
      >
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            sx={{ mr: 2, display: { sm: "none" } }}
          >
            <MenuIcon />
          </IconButton>
        </Toolbar>
      </AppBar>
      <Box
        component="nav"
        sx={{ width: { sm: drawerWidth }, flexShrink: { sm: 0 } }}
        aria-label="mailbox folders"
      >
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        <Drawer
          variant="temporary"
          open={open}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}
          sx={{
            display: { xs: "block", sm: "none" },
            "& .MuiDrawer-paper": {
              width: drawerWidth,
              backgroundColor: "#ffeddb",
            },
          }}
        >
          {drawer}
        </Drawer>
        <Drawer
          variant="permanent"
          sx={{
            display: { xs: "none", sm: "block" },
            "& .MuiDrawer-paper": {
              width: drawerWidth,
              backgroundColor: "rgb(10, 1, 18)",
            },
          }}
          open
        >
          {drawer}
        </Drawer>
      </Box>
    </Box>
  );
};

export default ResponsiveDrawer;
