import React, { useState, useEffect } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import ResponsiveDrawer from "../Components/ResponsiveDrawer";
import ContentCopyIcon from "@mui/icons-material/ContentCopy";
import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";

const SubAdminList = () => {
  const [subAdmins, setSubAdmins] = useState([]);
  const [loading, setLoading] = useState(true);
  const [openDrawer, setOpenDrawer] = useState(false);
  const [copyMessage, setCopyMessage] = useState("");
  const navigate = useNavigate();

  const handleDrawerToggle = () => {
    setOpenDrawer(!openDrawer);
  };

  useEffect(() => {
    const fetchSubAdmins = async () => {
      try {
        const token = localStorage.getItem("token"); // Retrieve token from localStorage

        // Make a GET request to fetch all sub-admins
        const response = await axios.get("api/auth/sub-admins", {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });

        // Set the fetched sub-admins data to state
        setSubAdmins(response.data);
        setLoading(false);
      } catch (error) {
        console.error("Error fetching sub-admins:", error);
      }
    };

    fetchSubAdmins();
  }, []);

  const handleLogout = async () => {
    try {
      // Send logout request to backend
      await axios.post("api/auth/logout");

      // Clear JWT token from local storage
      localStorage.removeItem("token");

      // Redirect to login page after successful logout
      navigate("/login");
      window.location.reload();
    } catch (error) {
      console.error("Logout error:", error);
    }
  };

  const handleCopyUsername = (username) => {
    navigator.clipboard
      .writeText(username)
      .then(() => {
        setCopyMessage(`SubAdmin "${username}" copied to clipboard.`);
      })
      .catch((error) => {
        console.error("Copy error:", error);
      });
  };

  const handleDeleteSubAdmin = async (userId) => {
    try {
      const token = localStorage.getItem("token");

      // Make a DELETE request to delete the sub-admin
      await axios.delete(`api/auth/delete-sub-admin/${userId}`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      // Remove the deleted sub-admin from the state
      setSubAdmins(subAdmins.filter((subAdmin) => subAdmin._id !== userId));
    } catch (error) {
      console.error("Error deleting sub-admin:", error);
    }
  };

  // Snackbar close handler
  const handleClose = () => {
    setCopyMessage("");
  };

  return (
    <div>
      <ResponsiveDrawer
        open={openDrawer}
        handleDrawerToggle={handleDrawerToggle}
        handleLogout={handleLogout}
      />

      <div
        className="app-mt-70 container p-2 fs-4 fw-bolder text-white"
        style={{ textAlign: "right" }}
      >
        Total Sub Admin: {subAdmins.length}
      </div>
      {loading ? (
        <p>Loading...</p>
      ) : (
        <ul className="container">
          {subAdmins.map((subAdmin, index) => (
            <li
              className="d-flex justify-content-between align-items-baseline "
              key={subAdmin._id}
            >
              <p className="text-white fw-bolder">{`${index + 1}.  ${
                subAdmin?.username
              }`}</p>
              <span
                style={{ cursor: "pointer", color: "#fff" }}
                onClick={() => handleCopyUsername(subAdmin?.username)}
              >
                <ContentCopyIcon />
              </span>
              <Snackbar
                open={!!copyMessage}
                autoHideDuration={6000}
                onClose={handleClose}
              >
                <MuiAlert
                  elevation={6}
                  variant="filled"
                  onClose={handleClose}
                  severity="success"
                >
                  {copyMessage}
                </MuiAlert>
              </Snackbar>
            </li>
          ))}
        </ul>
      )}
    </div>
  );
};

export default SubAdminList;
