import React, { useState, useEffect } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { Visibility, VisibilityOff } from "@mui/icons-material";
import ResponsiveDrawer from "../Components/ResponsiveDrawer";

const SubAdminRegistration = () => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [successMessage, setSuccessMessage] = useState("");
  const [errorMessage, setErrorMessage] = useState("");
  const [subAdmins, setSubAdmins] = useState([]);
  const [loading, setLoading] = useState(true);
  const [openDrawer, setOpenDrawer] = React.useState(false);
  const navigate = useNavigate();
  const [showPassword, setShowPassword] = useState(false);
  const handleDrawerToggle = () => {
    setOpenDrawer(!openDrawer);
  };

  useEffect(() => {
    const fetchSubAdmins = async () => {
      try {
        const token = localStorage.getItem("token"); // Retrieve token from localStorage

        // Make a GET request to fetch all sub-admins
        const response = await axios.get("api/auth/sub-admins", {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });

        // Set the fetched sub-admins data to state
        setSubAdmins(response.data);
        setLoading(false);
      } catch (error) {
        console.error("Error fetching sub-admins:", error);
      }
    };

    fetchSubAdmins();
  }, []);

  const handleLogout = async () => {
    try {
      // Send logout request to backend
      await axios.post("api/auth/logout");

      // Clear JWT token from local storage
      localStorage.removeItem("token");

      // Redirect to login page after successful logout
      navigate("/login");
      window.location.reload();
    } catch (error) {
      console.error("Logout error:", error);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      // Send a POST request to register the sub-admin
      const response = await axios.post(
        "api/auth/register-sub-admin",
        { username, password },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`, // Add token to headers
          },
        }
      );

      // Extract the token from the response
      const token = response.data.token;

      // Store the token in localStorage or your preferred storage
      localStorage.setItem("token", token);

      // Display success message if registration is successful
      setSuccessMessage(response.data.message);
      setErrorMessage("");
    } catch (error) {
      // Display error message if registration fails
      setErrorMessage(error.response.data.message);
      setSuccessMessage("");
    }
  };

  return (
    <div>
      <ResponsiveDrawer
        open={openDrawer}
        handleDrawerToggle={handleDrawerToggle}
        handleLogout={handleLogout}
      />

      <form className="form-login" onSubmit={handleSubmit}>
        <div className="fw-bolder fs-5 text-white">Register SubAdmin Form</div>
        {successMessage && (
          <p className="fs-6 text-success">{successMessage}</p>
        )}
        {errorMessage && <p className="fs-6 text-danger">{errorMessage}</p>}
        <div>
          <label className="fw-bolder text-white">Username:</label>
          <input
            type="text"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            required
          />
        </div>
        <div>
          <label className="fw-bolder text-white">Password:</label>
          <div className=" d-flex">
            <div>
              {" "}
              <input
                type={showPassword ? "text" : "password"} // Toggle password visibility
                placeholder="Password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                className="form-password" // Added Bootstrap form-control class
              />
            </div>
            <div className="input-group-text">
              {" "}
              {/* Added input-group-text class for Bootstrap styling */}
              {showPassword ? (
                <VisibilityOff onClick={() => setShowPassword(false)} />
              ) : (
                <Visibility onClick={() => setShowPassword(true)} />
              )}
            </div>
          </div>
        </div>
        <button type="submit">Register</button>
      </form>
    </div>
  );
};

export default SubAdminRegistration;
