import React, { useState } from "react";
import axios from "axios";
import ResponsiveDrawer from "../Components/ResponsivedrawerSubAdmin";
import { useNavigate } from "react-router-dom";

const SendMoneyForm = () => {
  const [username, setUsername] = useState("");
  const [amount, setAmount] = useState("");
  const [errorMessage, setErrorMessage] = useState("");
  const [successMessage, setsuccessMessage] = useState("");
  const navigate = useNavigate();

  const [openDrawer, setOpenDrawer] = React.useState(false);

  const handleDrawerToggle = () => {
    setOpenDrawer(!openDrawer);
  };
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const token = localStorage.getItem("token");

      await axios.post(
        "api/auth/send-user-amount",
        { username, amount },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );

      setUsername("");
      setAmount("");
      setErrorMessage("");
      setsuccessMessage("Amount Send Successfully");
    } catch (error) {
      setErrorMessage(error?.response?.data?.message);
    }
  };
  const handleLogout = async () => {
    try {
      // Send logout request to backend
      await axios.post("api/auth/logout");

      // Clear JWT token from local storage
      localStorage.removeItem("token");

      // Redirect to login page after successful logout
      navigate("/login");
      window.location.reload();
    } catch (error) {}
  };

  return (
    <div>
      <ResponsiveDrawer
        open={openDrawer}
        handleDrawerToggle={handleDrawerToggle}
        handleLogout={handleLogout}
      />
      <form className="form-login text-white" onSubmit={handleSubmit}>
        <div>
          <h2>Send Money to User</h2>
          {errorMessage && (
            <p className="text-danger text-center pb-0">{errorMessage}</p>
          )}
          {successMessage && (
            <p className="text-success text-center pb-0">{successMessage}</p>
          )}
        </div>
        <div>
          <label>Username:</label>
          <input
            type="text"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
          />
        </div>
        <div>
          <label>Amount:</label>
          <input
            type="number"
            value={amount}
            onChange={(e) => setAmount(e.target.value)}
          />
        </div>
        <button type="submit">Send</button>
      </form>
    </div>
  );
};

export default SendMoneyForm;
